.generated-password-card {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.password-label {
  font-size: 14px;
  color: var(--color-secondary);
  font-weight: 500;
  margin: 0;
}

.password-display {
  display: flex;
  align-items: center;
  gap: 12px;
}

.password-text {
  flex: 1;
  font-size: 16px;
  color: var(--color-text);
  word-break: break-all;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  background: var(--bg-white);
  padding: 12px;
  border-radius: var(--radius-btn);
  border: 1px solid var(--color-border);
  min-height: 20px;
}

.strength-label {
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 6px;
}

.strength-weak { color: #e57373; }
.strength-average { color: #ffb300; }
.strength-strong { color: var(--color-secondary); }
.strength-perfect { color: var(--color-primary); }

.copy-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  background: var(--color-primary);
  color: #fff;
  border: none;
  border-radius: var(--radius-btn);
  width: 48px;
  height: 48px;
  font-size: 1em;
  cursor: pointer;
  margin: 0;
  transition: background 0.2s;
  padding: 0;
  flex-shrink: 0;
}

.copy-btn:hover {
  background: #249e85;
}

.copy-btn svg {
  width: 22px;
  height: 22px;
}

.slider-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.slider-section label {
  font-size: 14px;
  color: var(--color-secondary);
  font-weight: 500;
  margin: 0;
}

.slider-section input[type="range"] {
  width: 100%;
  margin: 0;
  accent-color: var(--color-primary);
  height: 6px;
  border-radius: 3px;
}

.options-section {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.option-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  color: var(--color-text);
}

.option-row span {
  flex: 1;
  font-weight: 500;
}

.option-row input[type="checkbox"] {
  width: 24px;
  height: 24px;
  accent-color: var(--color-primary);
  cursor: pointer;
  flex-shrink: 0;
}

.regenerate-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-weight: 600;
}

.regenerate-btn svg {
  width: 20px;
  height: 20px;
}
