___CSS_LOADER_EXPORT___.push([module.id, `/* Settings Page Specific Styles */
.settings-page h1 {
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  color: var(--color-secondary);
  padding: 16px;
  margin: 0;
}

.profile-card {
  display: flex;
  gap: 16px;
  align-items: center;
}

.profile-card .user-icon {
  font-size: 24px;
  color: var(--color-primary);
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-light);
  border-radius: 50%;
  flex-shrink: 0;
}

.profile-card .user-info {
  flex: 1;
}

.profile-card .user-email {
  font-size: 16px;
  color: var(--color-text);
  font-weight: 600;
  margin: 0 0 4px 0;
}

.profile-card .user-subscription {
  font-size: 14px;
  color: var(--color-primary);
  margin: 0 0 2px 0;
}

.profile-card .user-status {
  font-size: 14px;
  color: var(--color-text-secondary);
  margin: 0;
}

.menu-list {
  display: flex;
  flex-direction: column;
  gap: var(--element-spacing);
}

.menu-item {
  height: 56px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 16px;
  cursor: pointer;
  transition: background-color 0.2s;
  border: none;
  text-align: left;
  width: 100%;
}

.menu-item:hover {
  background: var(--bg-light);
}

.menu-item .menu-icon {
  color: var(--color-secondary);
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  flex-shrink: 0;
}

.menu-item .menu-label {
  font-size: 16px;
  color: var(--color-primary);
  flex: 1;
  font-weight: 500;
}

.menu-item .menu-arrow {
  color: var(--color-accent);
  font-size: 16px;
  flex-shrink: 0;
}


