/* Search Bar Styles */
.search-bar {
  position: relative;
}

.search-input {
  width: 100%;
  height: 48px;
  border-radius: 20px;
  background: var(--bg-light);
  border: none;
  padding: 0 20px 0 48px;
  font-size: var(--font-size-base);
  box-sizing: border-box;
  color: var(--color-text);
  transition: box-shadow 0.2s;
}

.search-input::placeholder {
  color: var(--color-text-secondary);
}

.search-input:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(46, 174, 151, 0.2);
}

.search-bar::before {
  content: '';
  position: absolute;
  left: 16px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  color: var(--color-text-secondary);
  pointer-events: none;
}



/* Credential List */
.credential-list {
  display: flex;
  flex-direction: column;
  gap: var(--element-spacing);
  width: 100%;
}

.empty-state {
  padding: 12px;
  text-align: center;
  color: var(--color-text-secondary);
  font-size: 14px;
}

.suggestions-section {
  margin-bottom: 12px;
}

.suggestion-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 4px 0;
  border-radius: 8px;
}

.suggestion-list .credential-card {
  margin-bottom: 0;
  cursor: pointer;
  transition: transform 0.2s;
}

.suggestion-list .credential-card:hover {
  transform: translateX(4px);
}

.suggestion-list-count-0 {
  max-height: 40px;
}
.suggestion-list-count-1 {
  max-height: 60px;
}
.suggestion-list-count-2 {
  max-height: 120px;
}

.suggestion-list .credential-card {
  font-size: 14px;
  padding: 6px 8px;
  min-height: 36px;
  box-shadow: none;
  border: 1px solid var(--color-border, #e0e0e0);
}

.suggestion-list .credential-card:last-child {
  margin-bottom: 0;
}
